.Sidebar{
    position: sticky;
    position: -webkit-sticky;
    top: 0; /* required */
    height: 100vh;
    background-color: #363740;
    align-items: center;
    display: flex;
    flex: 1;
    flex-direction: column;
    width: 28%;
    min-width: 210px;
    .Sidebar-upstair{
        width: 100%;
        flex: 1;
        .Sidebar-Logo{
            display: flex;
            align-items: center;
            color: #D4D7E5;
            padding-left: 18%;
            margin-top: 5%;
            width: 100%;
            .Logo{
                background-color: #3751FF;
                border-radius: 50%;
                padding: 8px;
                .half-circle{
                    width: 12px;
                    height: 14px;
                    margin: 0 1px 0 2px;
                    background: linear-gradient(143.7deg, rgba(255, 255, 255, 0.7) 0%, #FFFFFF 100%);
                    border-top-right-radius: 110px;
                    border-bottom-right-radius: 110px;
                    border-bottom: 0;
                }
            }
            h2{
                padding-left: 6%;
                flex-shrink: 0;
            }
        }
        .options{
            width: 100%;
            padding-bottom: 4%;
            &:nth-child(2){
                margin-top: 8%;
                border-bottom: 1px solid #dfe0eb1c;
            }
            &:nth-child(3){
                margin-top: 5%;
            }        
            .op{
                .op_elem{
                    display: block;
                    padding: 7%;
                    text-decoration: none;
                    &:hover{
                        background-color: #3E4049;
                        border-left: 1px solid #DDE2FF;
                        img{
                            filter: brightness(2.25);
                        }
                    }
                    span{
                        padding-left: 16%;
                        color: #DDE2FF;
                        text-decoration: none;
                    }
                }
                .sub{
                    display: none;
                    flex-direction: column;
                    border-left: 1px solid rgba(221, 226, 255, 0.26);
                    margin-left: 25px;
                    .subMenu{
                        padding: 14px;
                        margin-left: 22px;
                        a{
                            text-decoration: none;
                            cursor: pointer;
                            color: rgba(221, 226, 255, 0.6);
                        }
                        &:before{
                            content: "";
                            position: absolute;
                            display: block;
                            width: 20px;
                            height: 1px;
                            background-color: rgba(221, 226, 255, 0.26);
                            margin-left: -36px;
                            margin-top: 9px;
                        }
                    }
                }
            }
        }
    }
}