.monitoring{
    background-color: #F5F6F9;
    padding: 0.4% 0 12% 0;
    width: 87%;
    @media (max-width: 1600px){
        width: 85%;
        overflow-x: hidden;
    }
    .stats{
        position: relative;
        top: 1%;
        width: 87%;
        margin: 0 auto;
        @media (max-width: 1600px){
            top: 0%;
            width: 96%;
        }
        .rankings{
            background-color: white;
            border-radius: 14px;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            .rank_header{
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 1%;
                border-bottom: 1px solid #E3E3E3;
                .filters{
                    width: 20%;
                    @media (max-width: 1600px){
                        width: 28%;
                    }
                }  
            }
            .rankings_content{
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 3%;
                .rank_elem{
                    display: flex;
                    align-items: center;
                    width: 30%;
                    &:nth-child(2){
                        padding-left: 4%;
                        width: 22%;
                        height: 80px;
                        margin-left: 13%;
                        border-left: 1px solid rgb(219, 219, 219);
                        border-right: 1px solid rgb(219, 219, 219);
                    }
                    .round_img{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: rgba(113, 59, 219, 0.12);
                        border-radius: 50%;
                        padding: 4%;
                        img{
                            width: 20px;
                            height: 20px;
                        }
                    }
                    .data{
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        margin-left: 2%;
                        &:nth-child(2){
                            margin-left: 6%;
                        }
                        span{
                            &:nth-child(1){
                                font-weight: 600;
                            }
                            &:nth-child(2){
                                font-weight: 200;
                            }
                        }
                        .addedProc{
                            font-weight: 300 !important;
                            margin-left: 2%;
                        }
                    }
                }
            }
        }
    }
    .trending{
        position: relative;
        top: 2%;
        margin: 0 auto;
        width: 87%;
        @media (max-width: 1600px){
            top: 1%; 
            width: 96%;
        }
        .trending_posts{
            display: flex;
            justify-content: space-between;
        }
    }
    .chart{
        position: relative;
        top: 4%;
        width: 87%;
        margin: 0 auto;
        @media (max-width: 1600px){
            top: 2%;
            width: 96%;
        }
        .chart_header{
            display: flex;
            justify-content: space-between;
            padding: 1%;
            border-bottom: 1px solid #E3E3E3;
            background-color: white;
            .filters_div{
                display: flex;
                align-items: center;
                width: 12%;
                @media (max-width: 1440px){
                    width: 32%;
                }
                .filters{
                    width: 68%;
                }    
                .dropdown{
                    display: flex;
                    flex-direction: column;
                    margin-left: -8%;
                    color: #6F52ED;
                    .mainDrop{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        border: 1px solid #DFDFDF;
                        width: 140%;
                        padding: 5%;
                        border-radius: 10px;
                        img{
                            position: relative;
                            right: 0;
                            justify-content: flex-end;
                        }
                    }
                    .drop{
                        margin: 2.2% 0 0 0.1%;
                        width: 8%;
                        border-bottom-left-radius: 10px;
                        border-bottom-right-radius: 10px;
                        background-color: white;
                        border-left: 1px solid #DFDFDF;
                        border-right: 1px solid #DFDFDF;
                        border-bottom: 1px solid #DFDFDF;
                        position: absolute;
                        z-index: 9999;
                        display: none;
                        span{
                            padding: 3%;
                            &:nth-child(1){
                                border-bottom: 1px solid #DFDFDF;
                            }
                        }
                    }
                    span{
                        display: block;
                    }
                }
            }
        } 
        .chart_div{
            background-color: white;
            border-bottom-left-radius: 20px;
            border-bottom-right-radius: 20px;
            padding: 1% 1% 3% 1%;
            box-shadow: 1px 2px 1px 1px #ebebeb;
        } 
    }
    .Trending-Topics{
        position: relative;
        top: 5%;
        width: 87%;
        border-radius: 2%;
        margin: 0 auto;
        overflow: hidden;
        @media (max-width: 1600px){
            top: 3%;
            width: 96%;
        }
        .topic{
            display: block;
            padding: 3% 0 0% 2.5%;
            width: 100%;
            color: #252733;
            font-weight: 600;

        }
        .keys{
            background-color: white;
            padding-top: 1%;
            margin: 6px;
            border-radius: 10px;
            box-shadow: 1px 2px 1px 1px #ebebeb;
        }
    }
}    