.posts_Div{
    display: flex;
    flex-direction: column;
    background-color: white;
    width: 32%;
    box-shadow: 1px 2px 1px 1px #ebebeb;
    border-radius: 10px;
    .posts{
        display: flex;
        flex-direction: column;
        border-radius: 15px;
        width: 100%;
        @media (max-width: 1600px){
            // width: 48%;
        }
        .rank_header{
            padding: 3% 2% 2% 2%; 
            border-bottom: 1px solid rgb(216, 216, 216);
            span{
                font-weight: 600;
            }
        }
        .post{
            height: 550px;
            overflow-y: scroll;
            overflow-x: hidden;
            .progress_bars{
                display: flex;
                margin: 0 auto;
                padding: 5%;
                justify-content: space-around;
                width: 90%;
                .bar{
                    height: 5px;
                    border-radius: 15px;
                    width: 100%;
                    margin-top: 8px;  
                }
                span{
                   color: #A6ACBE;
                }
                .positive{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                    margin-right: 6px;
                    span{
                        display: flex;
                        justify-content: flex-end;
                        position: relative;
                        right: 0;
                    }
                    .bar{
                        background-color: #33D69F;
                    }
                }
                .negative{
                    margin-left: 6px;
                    .bar{
                        background-color: #FF4C61;
                    }
                }
            }
            .imgPost{
                display: flex;
                flex-direction: column;
                justify-content: center;
                margin: 0 auto;
                header{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 90%;
                    margin: 0 auto;
                    .social-network{
                        width: 20%;
                        img{
                            width: 30px;
    
                        }
                    }
                    .profile_data{
                        display: flex;
                        justify-content: space-between;
                        width: auto;
                        a{
                            font-size: large;
                            font-weight: 800;
                            text-decoration: none;
                            color: rgb(106,130,173);
                            margin-right: 40%;
                        }
                        .avatar{
                            img{
                                width: 40px;
                                height: 40px;
                                border-radius: 50%;
                                margin-left: 6%;
                            }
                        }
                    }
                }
                .postText{
                    width: 90%;
                    margin: 0 auto;
                }
            }
        }
    }
    .arrows{
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        padding: 6% 3% 3% 3%;
        width: 86%;
        img{
            cursor: pointer;
        }
    }
}