.rank_score{
    .score_elem{
        margin-left: 40px;
        &:nth-child(2){
            margin-left: 220px;
            @media (max-width: 1366px){
                margin-left: 190px;
            }
        }
        .city_score{
            background-image: linear-gradient(to bottom, #6bd16f, #88d66f, #a1da71, #b8de75, #cde27b, #dada71, #e6d26b, #f1c967, #f8b159, #fd9753, #fe7c56, #fb5f5f);
            height: 130px;
            width: 15px;
            display: flex;
            justify-content: center;
            @media (max-width: 1600px){
                height: 100px;
            }
            &:before, &:after{
                content: '';
                position: absolute;
                height: 15px;
                width: 15px;
                z-index: 1;
            }
            &:before{
                margin-top: -10px;
                background-color: #6bd16f;
                border-top-left-radius : 50%;
                border-top-right-radius : 50%;
            }
            &:after{
                margin-top: 130px;
                background-color: #fb5f5f;
                border-bottom-left-radius : 50%;
                border-bottom-right-radius : 50%; 
                @media (max-width: 1600px){
                    margin-top: 100px;
                }                    
            }
            .score_point{
                position: relative;
                width: 6px;
                height: 6px;
                background-color: rgb(58, 58, 58);
                border-radius: 50%;
                z-index: 9999;
                &:after{
                    background-color: rgb(58, 58, 58);
                    content: "";
                    display: inline-block;
                    height: 1px;
                    position: relative;
                    vertical-align: middle;
                    width: 28px;
                    top: -7.5px;
                }
            }
            .color_point{
                position: absolute;
                margin-top: 45px;
                margin-left: 100px;
                width: 26px;
                height: 26px;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                @media (max-width: 1600px){
                    margin-top: 30px;
                }
                .line-between{
                    position: absolute;
                    right: 38px;
                    width: 1px;
                    background-color: black;
                }
                .outer-circle{
                    border: 1px solid #afafaf;
                    width: 18px;
                    height: 18px;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    div{
                        width: 10px;
                        height: 10px;
                        background-color: rgb(138, 190, 140);
                        border-radius: 50%;
                        &:before{
                            background-color: rgb(119, 119, 119);
                            content: "";
                            display: inline-block;
                            height: 1px;
                            position: relative;
                            vertical-align: middle;
                            width: 20px;
                            top: -6px;
                            right: 20px;
                        }
                    }
                }
            }
            .numb_score{
                position: absolute;
                left: 60px;
                width: 140px;
                span{
                    border: none !important;
                    &:nth-child(1){
                        font-size: 21px;
                    }
                    &:nth-child(2){
                        font-size: 16px;
                        color: #A6ACBE !important;
                    }
                }
            }
        }
    }
}
