.profile{
    header{
        display: flex;
        align-items: center;
        width: 87%;
        margin: 0 auto;
        justify-content: space-between;
        @media (max-width: 1600px){
            width: 96%;
        }
        div{
            display: flex;
            align-items: center;
        }
        .filters{
            min-width: 480px;
            h1{
                color: #7B7B7B;
            @media (max-width: 1600px){
                font-size: 25px;
            }
            }
            div{
                margin-left: 3vw;
                width: 100%;
                justify-content: space-between;
                span{
                    border: 1px solid #DFDFDF;
                    border-radius: 16%;
                    margin: 1%;
                    background-color: white;
                    font-size: 12px;
                    padding: 1.8% 3% 1.8% 3%;
                    color: #A6ACBE;
                    cursor: pointer;
                    &:nth-child(1){
                        background-color: #6F52ED;
                        color: white;
                    }
                }
            }    
        }
        .user-field{
            justify-content: space-between;
            width: 19%;
            @media (max-width: 1600px){
                width: 20.8%;
            }
            @media (max-width: 1366px){
                width: 27%;
            }
            .notifications_search{
                width: 32%;
                border-right: 1px solid rgb(218, 218, 218);
                height: 30px;
                margin-right: 9%;
                @media (max-width: 1600px){
                    margin-right: 7%;
                }
                .logout{
                    button{
                        background: none;
                        border: none;
                        cursor: pointer;
                    }
                }
                img{
                    width: 15px;
                    margin: 32%;
                }
            }
            h6{
                width: 135px;
                font-weight: 300;
                font-size: 16px;
            }
            .profile:nth-child(2){
                width: 100%;
                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    border: 1px solid grey;
                    padding: 1%;
                    margin-left: 6%;
                }
            }
        }
    }
}
