.block {
    margin: 25px 25px 0 0;
    border-radius: 5px;
    float: left;
    width: 300px;
    overflow: hidden;
  }
  
  .donut-chart-block {
    overflow: hidden;
  }
  
  .donut-chart {
    position: relative;
    width: 200px;
    height: 200px;
    margin: 2rem auto;
    border-radius: 100%
  }
  
  .donut-chart .center {
    background: white;
    position: absolute;
    top: 30px;
    left: 30px;
    height: 140px;
    width: 140px;
    border-radius: 50%;
  }
  
  .clip {
    border-radius: 50%;
    clip: rect(0px, 200px, 200px, 100px);
    height: 100%;
    position: absolute;
    width: 100%;
  }
  
  .item {
    border-radius: 50%;
    clip: rect(0px, 100px, 200px, 0px);
    height: 100%;
    position: absolute;
    width: 100%;
    font-family: monospace;
    font-size: 1.5rem;
  }