.Overview{
    background-color: #F5F6F9;
    padding: 0.4% 0 1% 0;
    width: 87%;
    .Overview-elements{
        display: flex;
        justify-content: space-around;
        width: 91%;
        margin: 0 auto;
        padding: 1.2% 0 1.2% 0;
        @media (max-width: 1600px){
            width: 98%;
        }
        .Rankings{
            background-color: white;
            border-radius: 2%;
            width: 47%;
            height: 290px;
            overflow: hidden;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            @media (max-width: 1600px){
                width: 47%;
                height: 290px;
            }
            @media (max-width: 1366px){
                height: 270px;
            }            
            span{
                display: block;
                padding: 3% 0 1.5% 2.5%;
                border-bottom: 1px solid #E3E3E3;
                width: 100%;
                &:nth-child(1){
                    color: #252733;
                    font-weight: 600;
                }
            }

        }
        .Trending-Topics{
            background-color: white;
            width: 45%;
            border-radius: 2%;
            overflow: hidden;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            @media (max-width: 1600px){
                width: 48%;
                height: 290px;
            }
            @media (max-width: 1366px){
                height: 270px;
            }          
            .topic{
                display: block;
                padding: 3% 0 1.5% 2.5%;
                border-bottom: 1px solid #E3E3E3;
                width: 100%;
                color: #252733;
                font-weight: 600;
            }
            .kaysDiv{
                height: 220px !important;
                @media (max-width: 1366px){
                    // height: 230px !important;
                }    
            }
        }
    }
    .map{        
        border: 20px solid white;
        border-radius: 2%;
        position: relative;
        margin-top: 2%;
        width: 83%;
        padding: 1%;
        margin: 0 auto;
        box-shadow: 1px 2px 1px 1px #ebebeb;
        font-family: 'Mulish', sans-serif;
        @media (max-width: 1600px){
            width: 91%;
        }
        .map_model{
            position: absolute;
            background-color: white;
            width: 34%;
            top: 8%;
            left: 10%;
            z-index: 9999;
            transition:visibility 0.1s linear,opacity 0.3s linear;
            @media (max-width: 1600px){
                width: 36.5%;
                top: 4%;
                left: 8%; 
            }
            header{
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: 1px solid #E3E3E3;
                width: 100%;
                span{
                    padding: 3.5%;
                    font-weight: 800;
                }
                .bar{
                    display: flex;
                    margin-right: 3%;
                    div{
                        width: 8px;
                        height: 5px;
                        border-radius: 50%;
                        background-color: #767676;
                        margin-right: 14%;
                    }
                }
            }
            .score{
                margin: 8% 0 8% 0;
            }
            .rank_score{
                margin-bottom: 4%;
            }
            .stats{
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                border-top: 1px solid #E3E3E3;
                width: 90%;
                margin: 0 auto;
                height: 300px;
                @media (max-width: 1600px){
                    height: 270px;
                }
                .stat_elem{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 5%;
                    width: 40%;
                    img{
                        width: 22px;
                        height: 22px;
                        @media (max-width: 1600px){
                            width: 18px;
                            height: 18px;    
                        }
                    }
                    div{
                        display: flex;
                        &:nth-child(1){
                            background-color: rgba(113, 59, 219, 0.12);
                            border-radius: 50%;
                            border: 14px solid #ECE7FA;
                            @media (max-width: 1600px){
                                border: 10px solid #ECE7FA;
                            }        
                        }
                        &:nth-child(2){
                            width: 90px;
                            margin-right: 3%;
                            margin-left: 3%;
                            flex-shrink: 0;
                            flex-grow: 0;
                        }
                        &:nth-child(3){
                            img{
                                width: 9px;
                                margin-left: 100%;
                            }
                        }
                        align-items: flex-start;
                        flex-direction: column;
                        span{
                            &:nth-child(1){
                                font-size: 20px;
                                font-weight: bold;
                                margin-bottom: 4%;
                                @media (max-width: 1600px){
                                    font-size: 17px;
                                }
                            }
                            &:nth-child(2){
                                font-size: 14px;
                                color: #A6ACBE;
                                @media (max-width: 1600px){
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .map_itself{
            padding: 2% 0 2% 0;
            .svg_map{
               background-color: #F5F5F5;
               padding: 0 12% 1% 12%;
               width: 70%;
               height: 67vh;
               display: flex;
               margin: 0 auto;
                   div{
                       margin: 0 auto;
                   }
            }
        }
    }
    .rank_score{
        display: flex;
        justify-content: space-between;
        margin-top: 8%;
        width: 25%;
    }
}