.login_page{
    width: 100%;
    height: 100vh;
    background-color: #F5F6F9;
    .login{
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        justify-content: center;
        width: 24%;
        height: 86%;
        div{
            display: flex;
            flex-direction: column;
            background: white;
            border-radius: 10px;
            padding: 10% 5% 35% 5%;
            height: 100px;
            border-bottom: 5px solid #EDEDF6;
            label{
                margin: 1% 0 1% 0;
                color: #A6ACBE;
                line-height: 21px;
                letter-spacing: 0.01em;
            }
            input{
                margin: 1% 0 1% 0;
                padding: 11px 15px;
                border-radius: 7px;
                border: 1px solid #DFDFDF;
                font-size: 16px;
                line-height: 20px;
                letter-spacing: 0.01em;
                font-family: 'Montserrat-Regular';
                -webkit-transition: 0.3s;
                transition: 0.3s;
                background: none;
                outline: none;
            }
            button{
                background: #6F52ED;
                font-size: 22px;
                font-weight: 500;
                margin-top: 3%;
                padding: 2%;
                border-radius: 10px;
                border: none;
                color: white;
            }
        }
    }
}