.municipal{
    background-color: #F5F6F9;
    padding: 0.4% 0 22% 0;
    width: 87%;
    .calls_center, .municipal_balance, .govmap, .education, .shefa, .immigration{
        position: relative;
        width: 87%;
        margin: 0 auto;
        @media (max-width: 1600px){
            width: 96%;
        }
    }
    .calls_center{
        top: 0.4%;
        @media (max-width: 1600px){
            top: 0%;
        }
    }
    .calls_center, .municipal_balance{
        .calls_center_div{
            background-color: white;
            border-radius: 12px;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            header{
                border-bottom: 1px solid #E3E3E3;
                .filters{
                    width: 20%;
                    padding: 1%;
                    @media (max-width: 1366px){
                        width: 32%;
                    }
                }
            }
            .rankings_content{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
                padding: 3%;
                .rank_elem{
                    display: flex;
                    align-items: center;
                    width: 30%;
                    &:nth-child(2), &:nth-child(5){
                        padding-left: 4%;
                        width: 22%;
                        height: 80px;
                        border-left: 1px solid rgb(219, 219, 219);
                        border-right: 1px solid rgb(219, 219, 219);
                        .round_img{
                            padding: 7%;
                        }
                    }
                    &:nth-child(2){
                        margin-left: 13%;
                    }
                    &:nth-child(5){
                        margin-left: -12.6%;
                        @media (max-width: 1600px){
                            margin-left: -12.2%;
                        }
                    }
                    &:nth-child(n+4) {
                        margin-top: 5%;
                     }
                    .round_img{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: rgba(113, 59, 219, 0.12);
                        border-radius: 50%;
                        padding: 5%;
                        img{
                            width: 20px;
                            height: 20px;
                        }
                    }
                    .data{
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        margin-left: 2%;
                        span{
                            &:nth-child(1){
                                display: flex;
                                align-items: center;
                                font-weight: 600;
                                img{
                                    width: 10px;
                                    margin-left: 4%;
                                }
                            }
                            &:nth-child(2){
                                font-weight: 200;
                            }
                        }
                        .addedProc{
                            font-weight: 300 !important;
                            margin-left: 2%;
                        }
                    }
                }
            }
            .chart{
                display: flex;
                justify-content: space-between;
                margin: 0 auto;
                width: 90%;
                .chartDiv{
                    width: 30%;
                }
                .chart_values{
                    display: flex;
                    flex-direction: column;
                    flex-wrap: wrap;
                    margin-top: 4%;
                    max-height: 220px;
                    width: 60%;
                    .chart_elem{
                        display: flex;
                        align-items: flex-start;
                        width: 30%;
                        margin: 2%;
                        .color{
                            width: 8px;
                            height: 8px;
                            border-radius: 50%;
                        }
                        .chart_data{
                            display: flex;
                            flex-direction: column;
                            margin-top: -3%;
                            margin-left: 5%;
                            .chart_procents{
                                display: flex;
                                margin-top: 8%;
                                span{
                                    &:nth-child(1){
                                        font-weight: 600;
                                    }
                                    &:nth-child(2){
                                        color: #9e9e9e; 
                                        margin-left: 4%;
          
                                    }                               
                                }
                            }
                        }
                    }
                }
            }
            .map_container{
                background-color: white;
                box-shadow: 1px 2px 1px 1px #ebebeb;
                border-radius: 12px;
                .map_modal{
                    position: absolute;
                    z-index: 9999;
                    margin: 4% 0 0 5%;
                    background-color: white;
                    box-shadow: 1px 2px 1px 1px #ebebeb;
                    border-radius: 12px; 
                    padding: 0% 0 2% 0;
                    width: 34%;
                    @media (max-width: 1600px){
                        width: 38%;                    
                    }
                    @media (max-width: 1366px){
                        width: 40%;                    
                    }
                    header{
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        padding: 4%;
                        border-bottom: 1px solid #DFDFDF;
                        .modal_titles{
                            display: flex;
                            justify-content: space-between;
                            width: 72%;
                            span{
                                &:nth-child(1){
                                    font-weight: 500;
                                }
                                &:nth-child(2){
                                    color: #6F52ED;
                                }
                            }
                        }
                        .bar{
                            display: flex;
                            justify-content: space-between;
                            width: 5.4%;
                            div{
                                width: 5px;
                                height: 5px;
                                border-radius: 50%;
                                background-color: grey;
                            }
                        }
                    }
                    .modal_content{
                        display: flex;
                        justify-content: space-between;
                        margin: 0 auto;
                        padding-top: 4%;
                        .left_modal_part{
                            padding-left: 6%;
                            .rank_elem{
                                display: flex;
                                align-items: center;
                                margin-top: 7%;
                                .round_img{
                                    display: flex;
                                    border-radius: 50%;
                                    padding: 5%;
                                    align-items: center;
                                    justify-content: center;
                                    img{
                                        width: 20px;
                                        height: 20px;
                                    }
                                }
                                .score{
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    width: 20px;
                                    height: 20px;
                                    border-radius: 50%;
                                    padding: 5%;
                                    .score_element{
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        border: 1px solid grey;
                                        border-radius: 50%;
                                        height: 14px;
                                        width: 14px;
                                        div{
                                            width: 10px;
                                            height: 10px;
                                            border-radius: 50%;
                                            background-color: #E18D93;
                                        }
                                    }
                                }
                                .data{
                                    display: flex;
                                    flex-direction: column;
                                    margin-left: 5%;
                                    span{
                                        &:nth-child(1){
                                           font-weight: 500;
                                        }
                                        &:nth-child(2){
                                            margin-top: 2%;
                                            color: #A6ACBE;
                                         }                                    
                                    }
                                }
                            }
                        }
                        .rankings{
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            width: 45%;
                            // height: 100px;
                           .chart_elem{
                               display: flex;
                               align-items: center;
                               .color_elem{
                                   width: 6px;
                                   margin-right: 2%;
                                   padding: 5px 0 5px 0;
                               }
                               .chart_data{
                                   margin-left: 6%;
                                   font-weight: 500;
                               }
                               .chart_procents{
                                   margin-top: 4%;
                                   span{
                                        &:nth-child(1){
                                            font-weight: 500;
                                            margin-right: 6%;
                                        }
                                        &:nth-child(2){
                                            color: #A6ACBE;
                                        }
                                   }
                               }
                           }
                        }
                    }

                }
                .map_itself{
                    padding: 2% 0 2% 0;
                    .svg_map{
                       background-color: #F5F5F5;
                       padding: 0 12% 1% 12%;
                       width: 70%;
                       height: 67vh;
                       display: flex;
                       margin: 0 auto;
                       div{
                           margin: 0 auto;
                       }
                    }
                }
             }
        }
    }
    .municipal_balance{
        top: 2%;
        @media (max-width: 1600px){
            top: 1%;
        }
        .municipal_chart{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            width: 80%;
            margin: 0 auto;
            .municipal_chart_elem{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding-top: 6%;
                &:nth-child(1){
                    border-right: 1px solid rgb(223, 223, 223);
                    padding-right: 21%;
                    @media (max-width: 1600px){
                        padding-right: 11vw;
                    }
                    @media (max-width: 1440px){
                        padding-right: 10vw;
                    } 
                    @media (max-width: 1366px){
                        padding-right: 7vw;
                    }                                          
                    .img_pad{
                        background: rgba(58, 215, 162, 0.11);
                    }
                    .amount_value{
                        span{
                            &:nth-child(1){
                                color: #33D69F;
                            }
                        }
                    }
                }
                &:nth-child(2){
                    .img_pad{
                        background: rgba(219, 59, 97, 0.1);
                    }
                    .amount_value{
                        span{
                            &:nth-child(1){
                                color: #E55376;
                            }
                        }
                    }
                }
                .amount{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-left: -24%;
                    width: 52%;
                    .img_pad{
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 8%;
                        border-radius: 50%;
                    }
                    .amount_value{
                        span{ 
                            &:nth-child(1){
                                font-size: 22px;
                            }
                            &:nth-child(2){
                                margin-top: 5%;
                                font-size: 12px;
                                color: #A6ACBE;
                            }
                        }
                        img{
                            margin-left: 22%;
                        }
                    }
                    div{
                        display: flex;
                        flex-direction: column;
                        span{
                            display: flex;
                            &:nth-child(1){
                                width: 20px;
                            }
                        }
                    }
                }
                .chart_values{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    flex-wrap: wrap;
                    margin-top: 4%;
                    max-height: 300px;
                    width: 120%;
                    .chart_elem{
                        display: flex;
                        align-items: flex-start;
                        width: 55%;
                        margin: 4% 0 4% 0;
                        .color{
                            width: 8px;
                            height: 8px;
                            border-radius: 50%;
                        }
                        .chart_data{
                            display: flex;
                            flex-direction: column;
                            margin-top: -3%;
                            margin-left: 5%;
                            .chart_procents{
                                display: flex;
                                margin-top: 8%;
                                span{
                                    &:nth-child(1){
                                        font-weight: 600;
                                    }
                                    &:nth-child(2){
                                        display: flex;
                                        align-items: center;
                                        color: #9e9e9e; 
                                        margin-left: 10%;
                                        img{
                                            margin-left: 4%;
                                            width: 10px;
                                        }     
                                    }                                    
                                }
                            }
                        }
                    }
                }
            }
        }
        .municipal_map_modal{
            position: absolute;
            z-index: 9999;
            margin: 4% 0 0 5%;
            background-color: white;
            box-shadow: 1px 2px 1px 1px #ebebeb;
