                            padding: 5%;
                            align-items: center;
                            justify-content: center;
                            img{
                                width: 20px;
                                height: 20px;
                            }
                        }
                        .score{
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            width: 20px;
                            height: 20px;
                            border-radius: 50%;
                            padding: 5%;
                            .score_element{
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                border: 1px solid grey;
                                border-radius: 50%;
                                height: 14px;
                                width: 14px;
                                div{
                                    width: 10px;
                                    height: 10px;
                                    border-radius: 50%;
                                    background-color: #E18D93;
