                                   font-weight: 500;
                                }
                                &:nth-child(2){
                                    margin-top: 2%;
                                    color: #A6ACBE;
                                 }                                    
                            }
                        }
                    }
                }
                .amount_container{
                    display: flex;
                    width: 90%;
                    margin: 0 auto;
                    margin-bottom: 8%;
                    .amount{
                        display: flex;
                        align-items: center;
                        width: 53%;
                        &:nth-child(1){
                            .img_pad{
                                background-color: #E8FAF4;
                            }
                            .amount_value{
                                span{
                                    &:nth-child(1){
                                        color: #33D69F;
                                    }
                                }
                            }
                        }
                        &:nth-child(2){
                            margin-left: 2%;
                            .img_pad{
                                background-color: #FAEBEE;
                            }
                            .amount_value{
                                span{
                                    &:nth-child(1){
                                        color: #E55376;
                                    }
                                }
                            }                            
                        }
                        .img_pad{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            padding: 4%;
                            margin: 2%;
                            border-radius: 50%;
                            img{
                                width: 30px;
                                height: 30px;
                            }
                        }
                        .amount_value{
                            span{ 
                                &:nth-child(1){
                                    font-size: 22px;
                                    @media (max-width: 1600px){
                                        font-size: 18px;
                                    }
                                }
                                &:nth-child(2){
                                    margin-top: 5%;
                                    font-size: 12px;
                                    color: #A6ACBE;
                                }
                            }
                            img{
                                margin-left: 22%;
                            }
                        }
                        div{
                            display: flex;
                            flex-direction: column;
                            span{
                                display: flex;
                                &:nth-child(1){
                                    width: 20px;
                                }
                            }
                        }
                    }
                }
                .modal_data{
                    display: flex;
                    justify-content: space-between;
                    margin: 0 auto;
                    width: 90%;
                    .rankings{
                        width: 45%;
                        height: 100px;
                        .chart_elem{
                             display: flex;
                            align-items: center;
                            .color_elem{
                               width: 6px;
                               margin-right: 2%;
                               padding: 5px 0 5px 0;
                             }
                            .chart_data{
                                margin-left: 6%;
                                font-weight: 500;
                                @media (max-width: 1600px){
                                    font-size: 16px;
                                }
                            }
                            .chart_procents{
                                display: flex;
                                margin-top: 4%;
                                span{
                                    &:nth-child(1){
                                        font-weight: 500;
                                        margin-right: 6%;
                                    }
                                    &:nth-child(2){
                                        display: flex;
                                        color: #A6ACBE;
                                        img{
                                            margin-left: 4%;
