    .education{
        top: 3%;
        @media (max-width: 1600px){
            top: 2%;
        }
        .map_container{
            background-color: white;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            border-radius: 12px; 
            .filters{
                display: flex;
                align-items: center;
                background-color: white;
                .mapFilters{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 1px solid #DFDFDF;
                    background-color: #F5F6F9;
                    border-radius: 4px;
                    margin: 1%;
                    padding: 0.3% 0 0.3%;
                    width: 8%;
                    select{
                        font-size: 15px;
                        border: none;
                        background-color: #F5F6F9;
                    }
                    @media (max-width: 1440px){
                       width: 15%;
                    }
                    &:nth-child(4){
                        img{
                            width: 12px;
                        }
                    } 
                    &:nth-child(6){
                        img{
                            width: 10px;
                        }
                    } 
                    img{
                        width: 15px;
                    }
                    span{
                        &:nth-child(2){
                            font-size: 14px;
                            margin-left: 5%;
                        }
                        &:nth-child(3){
                            font-size: 13px;
                            margin-left: 4%;
                            color: grey;
                        }
                    }
                }
            }
            .education_map_modal{
                .perc_Of_Graduates{
                    position: absolute;
                    background-color: white;
                    box-shadow: 1px 2px 1px 1px #ebebeb;
                    border-radius: 10px;
                    padding: 1%;
                    margin: 4% 0 0 5.6%;
                    z-index: 9999;
                    span{
                        &:nth-child(1){
                            font-weight: 500;
                        }
                    }
                    .elem{
                        margin-top: 5%;
                        display: flex;
                        align-items: center;
                        width: 60%;
                        div{
                            width: 10px;
                            height: 10px;
                            border-radius: 50%;
                        }
                        span{
                            margin-left: 10%;
                        }
                    }
                }
                .tichon_Hadash{
                    position: absolute;
                    background-color: white;
                    display: none;
                    box-shadow: 1px 2px 1px 1px #ebebeb;
                    width: 22%;
                    border-radius: 10px;
                    margin: 16% 0 0 16%;
                    opacity: 0.9;
                    z-index: 9999;
                    @media (max-width: 1600px){
                        margin: 18% 0 0 16%;
                        width: 300px;
                    }
                    header{
                        padding: 4%;
                        border-bottom: 1px solid #DFDFDF;
                        font-weight: 600;
                    }
                    .tichon_Hadash_container{
                        padding: 4%;
                        display: flex;
                        justify-content: space-between;
                        .elem{
                            width: 45%;
                            div{
                                display: flex;
                                flex-direction: column;
                            }
                            .college{
                                margin-left: 12%;
                                span{
                                    &:nth-child(1){
                                        color: #30c593;
                                        font-weight: 600;
                                        margin-bottom: 2%;
                                    }
                                    color: #A6ACBE;
                                }
                            }
                            .name{
                                flex-direction: row;
                                align-items: center;
                                margin-top: 24%;
                                margin-bottom: -8%;
                                width: 130%;
                                div{
                                    width: 10px;
                                    height: 10px;
                                    border-radius: 50%;
                                    margin-right: 5%;
                                }
                            }
                        }
                    }
                }
            }
            .map_itself{
                padding: 2% 0 2% 0;
                .svg_map{
                   background-color: #F5F5F5;
                   padding: 0 12% 1% 12%;
                   width: 70%;
                   height: 67vh;
                   display: flex;
                   margin: 0 auto;
                       div{
                           margin: 0 auto;
                       }
                }
            }
        }
        .markercluster-map {
            height: 600px;
          }
    }
    .govmap{
        top: 4%;
        @media (max-width: 1600px){
            top: 3%;
        }        
        .map_container{
            background-color: white;
            box-shadow: 1px 2px 1px 1px #ebebeb;
            padding: 2% 2% 2% 2%;
            border-radius: 12px; 
            #map{
                height: 600px;
                width: 100%;
            }
        }    
    }
    .shefa{
        top: 5%;
        @media (max-width: 1600px){
            top: 4%;
        }
        .map_container{
            background-color: white;
