                padding: 1.8% 1.3% 1.8% 1.3%;
            }
           .map_itself{
                padding: 2% 0 2% 0;
                .svg_map{
                    background-color: #F5F5F5;
                    padding: 0 12% 1% 12%;
                    width: 70%;
                    height: 67vh;
                    display: flex;
                    margin: 0 auto;
                    div{
                        margin: 0 auto;
                    }
                }
            }
        }
    }
    .immigration{
        top: 5.5%;
        padding-bottom: 4%;
        @media (max-width: 1600px){
            top: 5%;
        }
        .immigration_container{
            background-color: white;
