                        }
                    }
                    .green_home_icon, .red_home_icon{
                        display: flex;
                        padding: 4%;
                        border-radius: 50%;
                        img{
                            width: 26px;
                        }
                    }
                    .green_home_icon{
                        background-color: rgba(58, 215, 162, 0.11);;
                    }
                    .red_home_icon{
                        background-color: rgba(219, 59, 97, 0.1);
                    }
                    &:nth-child(1){
                        border-right: 1px solid #E3E3E3;
                        padding-right: 6%;
                        @media (max-width: 1600px){
                            padding-right: 8%;
                        }
                        div{
                            span{
                                &:nth-child(1){
                                    color: #33D69F;
                                }
                            }
                        }
                    }
                    &:nth-child(2){
                        div{
                            span{
                                &:nth-child(1){
                                    color: #E55376;
                                }
                            }
                        }
                    }  
                    div{
                        display: flex;
                        flex-direction: column;
                        margin-right: 4%;
                        span{
                            &:nth-child(2){
                                margin-top: 3%;
                                color: #A6ACBE;
                            }
                        }
                    }
                }
            }
        }
    }
    .gradient_elem{
        position: absolute;
        display: flex;
        left: 6%;
        bottom: 3%;
        .gradient{
            border: 4px solid white;
            background-image: linear-gradient(to bottom, #6bd16f, #88d66f, #a1da71, #b8de75, #cde27b, #dada71, #e6d26b, #f1c967, #f8b159, #fd9753, #fe7c56, #fb5f5f);
            height: 140px;
            width: 20px;
            border-radius: 15px;
            @media (max-width: 1600px){
                height: 120px;
                width: 14px;
            }
        }
        .data{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-left: 6%;
            div{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 40px;
                border-radius: 5px;
                padding: 1%;
                background-color: white;
                box-shadow: 1px 2px 1px 1px #ebebeb;
            }
        }
    }
}    